import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'
import conf from '../../../../conf.js';
import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';
// import url from '../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import { format } from 'date-fns';
import { storage } from "../../../../firebase/firebase";
import Flatpickr from "react-flatpickr"
import ModalMessage from "../../../pages/messages/ModalMessage"
import { French } from "flatpickr/dist/l10n/fr.js";

const animatedComponents = makeAnimated();
// const cities = require('all-the-cities');

registerLocale('es', es)

class ModifierPublicite extends Component {
    constructor(props) {
        super(props)
        this.state = {
          ok:"",
          files_preview:"",
          files_name:{file:""},
          multimedia:{file:""},
          multiUpdate:{},
          date_debut:new Date(),
          date_fin:new Date(),
          debut:"",
          fin:"",
          show:false,
          success:true,
          boutton:"OK",
          message:"Publicit Modifi avec succs !!",
          titre:"Message modification de publicit"
        }
    }

  componentDidMount() {

    let  token=  JSON.parse(localStorage.getItem('token_auth'))
    var id=localStorage.getItem("idPub")
    const requestOptions = {
      method: 'GET',
      headers: { 
      'Authorization':'Bearer '+token.token,
    }
  }

    fetch(conf.url+"acceuil/pub/pubs/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      $("#endroit").val(data.endroit)
      $("#url").val(data.urlPub)
       this.setState({debut:format(new Date(data.dateDebut),'dd/MM/yyyy'),fin:format(new Date(data.dateFin),'dd/MM/yyyy')})
       
       console.log("ok",data)
       
       if(data.multimedia.file!=undefined){
        storage.ref("images")
        .child(data.multimedia.file).getDownloadURL()
        .then((url) => {
          
            this.setState({files_preview:url})
            this.setState({multiUpdate:data.multimedia})
        
        })
       }
       
     }
     
    );

    $("input[data-preview]").change(() =>{
      var data=[]
      console.log("fiel =====>",Array.from(document.getElementById("autreFile").files))
      var date=new Date()
      
      var my_files = Array.from(document.getElementById("autreFile").files)
        var fileNom=date.getFullYear()+""+date.getMonth()+""+date.getDay()+""+date.getSeconds()+""+date.getMilliseconds()+""+my_files[0].name
        this.setState({files_name:{file:my_files[0],fileName:fileNom},multimedia:{file:fileNom}})
        
        var fileReader = new FileReader();
        fileReader.readAsDataURL(my_files[0]);
        fileReader.onload = (fileEvent)=> {
          this.setState({files_preview:fileEvent.target.result})
    
        };

    
    });

  }

  handleUpdatePub=()=>{
    var media=null
    var id=localStorage.getItem("idPub")
    let  token=  JSON.parse(localStorage.getItem('token_auth'))
    var data= {
      "id":id,
      "dateDebut": this.state.date_debut,
      "dateFin":this.state.date_fin,
      "endroit":$("#endroit").val(),
      "urlPub":$("#url").val(),
      "multimedia":this.state.multiUpdate
    }

    if(this.state.files_name.file!=""){
     media=this.state.multimedia
     storage.ref("images/"+this.state.files_name.fileName).put(this.state.files_name.file).then(()=>{})
     data= {
      "id":id,
      "dateDebut": this.state.date_debut,
      "dateFin":this.state.date_fin,
      "endroit":$("#endroit").val(),
      "urlPub":$("#url").val(),
      "multimedia":media, 
     
    }
   }
   

   

  console.log("data a transmettre",data)

  const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };

  fetch(conf.url+"pub/update",requestOptions)
  .then((response) => response.json())
  .then((data) => {
     console.log("okkk====>",data)
     try{
      if(data.id){
       this.setState({show:true,success:true});
      }else{
       this.setState({show:true,success:false});
      }
   }catch(err){
     this.setState({show:true,success:false});
   }
  
   }
   
  );

  }

  handleDateDebut= (date)=> {

    if(date.length!=0){
      
      this.setState({date_debut:date[0]});
    }
  
    
  }

  handleDateFin= (date)=> {

    if(date.length!=0){
     
      this.setState({date_fin:date[0]});
    }
  
    
  }

  handleRetour=()=>{
    this.setState({ok: <Redirect to='/dynamic-form-accueil'/>});
  }

  handleValidate=()=>{
    if(this.state.success){
      this.setState({ok: <Redirect to='/dynamic-form-accueil'/>});
    }else{
      this.setState({show:false})
    }
    
  }

  render() {

  const { pick } = this.state
  const { selectedGroup } = this.state
  const { selectedLangue } = this.state
  const animatedComponents = makeAnimated();

  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}
             
             <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />

            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Modifier la publicit</h1>
                  {/* </CCardHeader> */}


                  <CardBody>
                    
                    <Row  className="mt-3"> 
                      <Col md="6">
                        <label>Date de dbut</label>
                        <div className="docs-datepicker">
                          <InputGroup>
                            <Flatpickr
                              value={pick}
                              className="form-control d-block"
                              placeholder={this.state.debut}
                              options={{
                                locale:French,
                                // mode: "range",
                                altInput: true,
                                // altFormat: "F j, Y",
                                altFormat: "d/M/Y"
                              }}
                               onChange={this.handleDateDebut}
                            />
                            <div className="input-group-append">
                              <button
                                type="button"
                                className="btn btn-outline-secondary docs-datepicker-trigger"
                                disabled
                              >
                                <i
                                  className="fa fa-calendar"
                                  aria-hidden="true"
                                />
                              </button>
                            </div>
                          </InputGroup>
                          <div className="docs-datepicker-container" />
                        </div>
                      </Col>
                      <Col md="6">
                        <label>Date de fin</label>
                        <div className="docs-datepicker">
                          <InputGroup>
                            <Flatpickr
                              value={pick}
                              className="form-control d-block"
                              placeholder={this.state.fin}
                              options={{
                                locale:French,
                                // mode: "range",
                                altInput: true,
                                // altFormat: "F j, Y",
                                altFormat: "d/M/Y"
                              }}
                              onChange={this.handleDateFin}
                            />
                            <div className="input-group-append">
                              <button
                                type="button"
                                className="btn btn-outline-secondary docs-datepicker-trigger"
                                disabled
                              >
                                <i
                                  className="fa fa-calendar"
                                  aria-hidden="true"
                                />
                              </button>
                            </div>
                          </InputGroup>
                          <div className="docs-datepicker-container" />
                        </div>
                      </Col>
                    </Row>
                    <Row className="mt-3">
                      <Col md="6">
                        <label>Url</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="url"
                          placeholder="Url du Site"
                        />
                      </Col>
                    </Row>

                    <Row className="mt-3">
                      <Col md="6">
                        <label>Endroit</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="endroit"
                          placeholder="Endroit de la publicit"
                        />
                      </Col>
                      <Col md="6">
                        <label>Image de la publicit</label>
                        <Input
                          className="form-control"
                          type="file"
                           
                          data-preview=".preview"
                          placeholder="Image de la publicit"
                          id="autreFile"
                        />
                      </Col>
                    </Row>

                    
                    <Row className="mt-2">
                             <Col md="12">
                                    <Card>
                                       
                                        <CardBody>
                                            <img style={{width:"900px",heigth:"200px"}} className="card-img-top" src={this.state.files_preview} alt="Image"/>
                                        </CardBody>
                                        
                                       
                                    </Card>
                              </Col>
                      </Row>

                    
                    
                    

                    
                    <Row className="justify-content-start mt-5">
                      <Col md="6" className="mt-1">
                        <Button id="initial" onClick={this.handleUpdatePub}  color="success" block className="b-lg" >
                          Modifier la publicit
                        </Button>
                        {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button> */}
                      </Col>
                      <Col md="6" className="mt-1">
                        <Button  onClick={this.handleRetour} color="secondary" block className="b-lg">
                          Annuler
                        </Button>
                      </Col>
                    </Row>


                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default ModifierPublicite